<template>
  <div>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>

    <div class="footer">
      <van-tabbar v-model="activeIndex">
        <van-tabbar-item v-for="(item, index) in menus" :key="index" @click="handleClick(item)">
          <i slot="icon" class=" iconfont" :class="item.icon"></i>
          <span>{{item.name}}</span>
        </van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import "./style/index.less";

export default {
  name: "index",
  data () {
    return {
      linkState: 0,
      activeIndex: 0,
      menus: [
        { name: "课堂信息", icon: "icon-kejian1", url: "/course-record/record/view" },
        { name: "课堂互动", icon: "icon-kongzhitai", url: "/course-record/record/power" },
        { name: "课堂资源", icon: "icon-fenlei", url: "/course-record/record/source" },
        { name: "课堂实况", icon: "icon-weike", url: "/course-record/record/video" }
      ]
    };
  },
  created () {
    this.$gatewaySignalR.startGatewaySignalR();
  },
  methods: {
    handleClick (item) {
      this.$router.replace(item.url).catch(() => {
      });
    },
    reFreshIndex () {
      this.activeIndex = this.menus.findIndex(r => r.url === this.$route.path);
      if (this.menus[this.activeIndex].switchType || this.menus[this.activeIndex].switchType === "0") {
        this.$gatewayHttp.broadcast.switchPage({ function: this.menus[this.activeIndex].switchType });
      }
    }
  },
  watch: {
    $route: {
      handler: "reFreshIndex",
      immediate: true
    }
  }
}
</script>

<style lang="less" scoped>
.footer {
  /deep/ .van-tabbar-item--active {
    color: #00cbab;
  }
}
</style>
